import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { Carousel } from 'react-responsive-carousel';
import { push, goBack } from 'react-router-redux';
import './MarketPlace.css';
import ShowMoreText from 'react-show-more-text';
import ZoomImage from '../../../common/Components/ZoomImage';
import Image from '../../../common/Components/Image';
import { GET_RESELLING_PRODUCTS } from '../../../constants/actionTypes';
import agent from '../../../agent';

class ResellingProduct extends Component {
  constructor(props) {
    super(props);
    this.state = {
      product: { product: {} },
    };
  }

  componentDidMount() {
    const { product } = this.props;
    console.log(product);
    if (product) {
      this.setState({ product });
    }
  }

  componentWillReceiveProps(np) {
    const { product } = this.props;
    if (np.product && np.product !== product) {
      this.setState({ product: np.product });
    }
  }

  render() {
    const { product, resell_margin } = this.state;
    const {
      category, cod_available, description, disc_price, estimated_delivery, images, meta_data, name, online_available, preview_images,
    } = product;

    return (
      <div className="reselling-product-div">
        <div className="product-header">
          {
            name
          }
          <div className="margin">
            margin: 50 Rs
          </div>
        </div>
        <div className="product-img-carousel">
          <Carousel className="" style={{ background: 'white' }} showArrows={false} showIndicators={false} showThumbs={false}>
            { images ? images.map((img, i) => (
              <ZoomImage>
                <Image src={img} alt="" overlaySrc={preview_images[i]} key={i} />
              </ZoomImage>
            )) : null}
          </Carousel>
        </div>
        <div className="product-footer">
          <div className="content">
            <div>
              <b>{name}</b>
              <p>{`${disc_price} Rs.`}</p>
              <ShowMoreText

                lines={1}
                more="See more"
                less="See less"
                anchorClass="show-more"

                expanded={false}
                width={150}
              >
                <small>
                  {' '}
                  {description}
                  {' '}
                </small>
              </ShowMoreText>

            </div>


          </div>
          <button type="button">
                Resale Product
          </button>
        </div>

      </div>
    );
  }
}


const mapStateToProps = (state) => ({

});

const mapDispatchToProps = (dispatch) => ({
  getResellingProducts: (pageNumber) => dispatch({ type: GET_RESELLING_PRODUCTS, pageNumber, payload: agent.ProductsPage.getResellingProducts(pageNumber) }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ResellingProduct);
