import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { push, goBack } from 'react-router-redux';
import './MarketPlace.css';

import { GET_RESELLING_PRODUCTS } from '../../../constants/actionTypes';
import agent from '../../../agent';
import ResellingProduct from './ResellingProduct';

class MarketPlace extends Component {
  constructor(props) {
    super(props);
    this.state = {
      resellingProducts: [],
    };
    this.renderProducts = this.renderProducts.bind(this);
  }

  componentDidMount() {
    const { resellingProducts, getResellingProducts } = this.props;
    if (resellingProducts) {
      this.setState({ resellingProducts });
    }
    getResellingProducts(1);
  }

  componentWillReceiveProps(np) {
    console.log(np);
    const { resellingProducts } = this.props;
    if (np.resellingProducts && np.resellingProducts !== resellingProducts) {
      this.setState({ resellingProducts: np.resellingProducts });
    }
  }

  renderProducts() {
    const { resellingProducts } = this.state;
    return resellingProducts.map((product) => <ResellingProduct product={product} key={product.id} />);
  }

  render() {
    return (
      <div className="market-place-div">
        <div className="market-place-content-div">
          {
                this.renderProducts()
            }
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => ({
//   user: state.AuthReducer.user,
//   products: state.ProductsPageReducers.products,
//   shopDetails: state.ProductsPageReducers.shopDetails,
//   publicPageProducts: state.HomePageReducers.publicPageProducts,
//   chosenCategory: state.ProductsPageReducers.chosenCategory,
//   publicPageChosenCategory: state.HomePageReducers.publicPageChosenCategory,
//   routingLocation: state.router.location,
  resellingProducts: state.ProductsPageReducers.products,
  // categories: state.ProductsPageReducers.categories,
});

const mapDispatchToProps = (dispatch) => ({
  getResellingProducts: (pageNumber) => dispatch({ type: GET_RESELLING_PRODUCTS, pageNumber, payload: agent.ProductsPage.getResellingProducts(pageNumber) }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(MarketPlace);
